// API

export default {
    async fetchSkills() {
        var res = await fetch('/skills', {
            method: 'POST',
            body: JSON.stringify({
                "skill_names": ["Deep learning"], 
                "account_info": [["academic","1932464894"], ["stackoverflow", "395857"], ["github", "brannondorsey"]], 
                "appid": "4652c039245001755d3808ac464ea656"
            }),
            headers: {
                'Content-Type': 'application/json'
            }
        })
        var json = await res.json()
        return json
    },
    async postassess(data) {
        var res = await fetch('/assess_candidate', {
            method: 'POST',
            body: JSON.stringify(data),
            headers: {
                'Content-Type': 'application/json'
            }
        })
        var json = await res.json()
        return json
    }
}
